export default {
	data() {
		return {
			tabbar: [],
			height: 0, //scroll-view高度
			currentTab: 0, //预设当前项的值
			scrollTop: 0, //tab标题的滚动条位置
			info:[],   //轮播图
			tabgoodsList:[],   //对应得tab信息列表
			listAll:[],  //显示专场推荐
		}
	},
	onLoad: function(options) {
		uni.getSystemInfo({
			success: (res) => {
				let header = 92;



				this.height = res.windowHeight - uni.upx2px(header)
			}
		});
		// 获取侧边栏信息
		this.$http.getSlideAll({}).then(res => {
			// console.log("res", res);
			this.tabbar = res.data
		})
		// 获取轮播图信息
		this.$http.getLunImgs({}).then(res => {
			this.info = res.data
		});
		// 获取初次默认数据
		this.$http.lookDetailClass({
			class:1
		}).then(res => {
			// console.log("all", res);
			this.tabgoodsList = res.data
		})
		// 获取专场推荐数据
		this.$http.getClassList({}).then(res => {
			// console.log('res====', res);
			var list = res.data
			this.listAll = list.slice(5,11)
		})
	},
	methods: {
		// 点击标题切换当前页时改变样式
		swichNav: function(e) {
			console.log("====" ,e);
			var that = this
			let cur = e.currentTarget.dataset.current;
			that.tabbar.forEach(function(item, i) {
				if(cur == i) {
					// console.log("====", item.classid);
					that.$http.lookDetailClass({
						class:item.classid
					}).then(res => {
						console.log("all", res);
						that.tabgoodsList = res.data
					})
				}
			})
			if (that.currentTab == cur) {
				return false;
			} else {
				that.currentTab = cur;
				that.checkCor();
			}
		},
		//判断当前滚动超过一屏时，设置tab标题滚动条。
		checkCor: function() {
			let that = this;
			if (that.currentTab > 7) {
				that.scrollTop = 500
			} else {
				that.scrollTop = 0
			}
		},
		detail(e) {
			uni.navigateTo({
				url: '../extend-view/productDetail/productDetail?goodsid='+e
			})
		},
		// 点击进入商品详情
		prodectail(e) {
			uni.navigateTo({
				url: '../productDetail/productDetail?goodsid='+e
			});
		},
		search: function() {
			uni.navigateTo({
				url: '../news-search/news-search'
			})
		}
	}
}
